<template>
  <van-tabbar
    v-model="activeTabbar"
    active-color="#0055ff"
  >
    <van-tabbar-item :to="{name: 'home'}">
      <span>首页</span>
      <img
        slot="icon"
        slot-scope="props"
        :src="props.active ? HomeIcon.active : HomeIcon.normal"
      >
    </van-tabbar-item>
    <van-tabbar-item :to="{name: 'classification'}">
      <span>分类</span>
      <img
        slot="icon"
        slot-scope="props"
        :src="props.active ? SortIcon.active : SortIcon.normal"
      >
    </van-tabbar-item>
    <van-tabbar-item :to="{name: 'order'}">
      <span>订单</span>
      <img
        slot="icon"
        slot-scope="props"
        :src="props.active ? OrderIcon.active : OrderIcon.normal"
      >
    </van-tabbar-item>
    <van-tabbar-item :to="{name: 'my'}">
      <span>我的</span>
      <img
        slot="icon"
        slot-scope="props"
        :src="props.active ? MyIcon.active : MyIcon.normal"
      >
    </van-tabbar-item>
  </van-tabbar>
</template>

<script>
export default {
  props: {
    Tabbar: Number
  },
  data() {
    return {
      activeTabbar: this.Tabbar,
      HomeIcon: {
        normal: "/static/image/Nhome.png",
        active: "/static/image/home.png"
      },
      SortIcon: {
        normal: "/static/image/Nsort.png",
        active: "/static/image/sort.png"
      },
      OrderIcon: {
        normal: "/static/image/Norder.png",
        active: "/static/image/order.png"
      },
      MyIcon: {
        normal: "/static/image/Nmy.png",
        active: "/static/image/my.png"
      }
    };
  }
};
</script>

<style lang="less" scoped>
</style>



